.calendar {
  justify-content: center;
  margin: 0;
  padding: 10px;
  display: flex;
}

.calendar__month {
  flex-wrap: wrap;
  gap: 1px;
  width: 706px;
  display: flex;
}

.calendar__day {
  opacity: 0;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #eee;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  transition: all .5s;
  display: flex;
}

.calendar__day:hover {
  background-color: #ffbfcb;
  transition: all .5s;
  transform: translateY(-20px);
}

